<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card class="mb-base">
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>
      <div id="loading-container" class="vs-con-loading__container">
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('SALUTATION') }}</label>
            <vs-input class="w-full" v-model="user.salutation"/>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('FIRST_NAME') }}</label>
            <vs-input class="w-full" v-model="user.firstName"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('MIDDLE_NAME') }}</label>
            <vs-input class="w-full" v-model="user.middleName"/>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('LAST_NAME') }}</label>
            <vs-input class="w-full" v-model="user.lastName"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('SUFFIX') }}</label>
            <vs-input class="w-full" v-model="user.suffix"/>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('MOTHERS_MAIDEN_NAME') }}</label>
            <vs-input class="w-full" v-model="user.mothersMaidenName"/>
          </div>
        </div>

        <div class="nv-row">
          <div class="nv-col w-full">
            <div class="flex justify-end">
              <vs-button class="px-5 py-3" @click="modifyUserProfile" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import SecurityService from '@/services/nucleus/SecurityService'

export default {
  name: 'profile-modify',
  mixins: [AlertMixin, LoadingMixin],
  data: function () {
    return {
      user: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.profile({
        userId: this.$store.state.user.userId
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.user = response.data || {}
      })
    },
    modifyUserProfile () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.modifyUserProfile({
        salutation: this.user.salutation,
        firstName: this.user.firstName,
        middleName: this.user.middleName,
        lastName: this.user.lastName,
        suffix: this.user.suffix,
        mothersMaidenName: this.user.mothersMaidenName
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
